.nav{
  padding: 0 20%;
  line-height: 60px;
  border-bottom: 2px solid red;
  font-weight: bold;
}
.netease-Recommend{
  margin: 0 20%;
  padding: 10px 0;
  display: flex;
  flex-wrap: wrap;
  border-left: 1px solid #999999;
  border-right: 1px solid #999999;
}
.netease-Recommend li {
  width: 20%;
  list-style: none;
  font-size: calc(10px + 0.5vmin);
  display: flex;
  flex-direction: column;
  align-items: center;
}

.netease-Recommend li img{
    width: 80%;
    height: auto;
}
.netease-Recommend li:hover {
 background-color: rgba(red, green, blue, 1);
}
audio{
  width: 100%;
  
}