.App {
  min-width: 1300px;
  max-width: 100vw;
}

.App-header {
  background-color: #000;
  min-height: 50px;
  display: flex;
  align-items: center;
  justify-content: space-around;
  font-size: calc(10px + 0.5vmin);
  padding: 0 20%;
}

.navsty{
  color: white !important;
}

.App-header-logo img{
  width: calc(30px + 1vmin);
}

.App-header a{
  color: #999999;
  text-decoration: none;
}

.App-footer{
  width: 100%;
  height: 70px;
  background-color: #282c34;
  color: white;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 0.5vmin);
}

.Index{
  min-height: calc(100vh - 120px);
}
/* Index 首页 */
.Index-title{
  text-align: center;
  font-size: calc(20px + 5vmin);
  font-weight: bold;
  margin: 50px 0;
  background: -webkit-linear-gradient(top, rgba(207, 24, 0,1),rgba(68, 156, 72,1),rgba(13, 138, 187,1));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  cursor: pointer;
}

/* netease 网易云页面 */

.swiper-slide img{
  width: 100%;
  height: auto;
}

.swiper-slide{
  display: flex;
  justify-content: center;
  align-items: center;
}

.netease-header{
  padding: 0 25%;
  margin: 0 auto;
  background-color: #999999;
}

